body {
  font-family: sans-serif;
}

#loadMore {
  margin: 50px auto 0;
  display: block;
}

#loadMore:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

#loadMore.hidden {
  display: none;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.photo-card {
  width: 400px;
  padding: 20px;
}

.image {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.info {
  display: flex;
}

